import coca from '../images/coca.png';
import images from './images';

const makePagination = (start, length, found) => {
    let nbPage = 0
    if (found%length === 0){
        nbPage = found/length;
    }else {
        nbPage = parseInt(found/length)+1;
    }

    const section = document.createElement("section");
    section.setAttribute("aria-label", "Page navigation example");
    const ul = document.createElement("ul");
    ul.classList.add("pagination", "justify-content-center");

    const previous = document.createElement("li");
    previous.classList.add("page-item", "disabled");

    const spanPrevious = document.createElement("span");
    spanPrevious.classList.add("page-link");
    spanPrevious.setAttribute("tabindex", "-1");
    spanPrevious.setAttribute("aria-disabled", "true");
    spanPrevious.innerText = "Précédent";

    previous.append(spanPrevious);
    ul.append(previous);

    for (let index = 0; index < nbPage; index++) {
        const li = document.createElement('li');
        li.classList.add("page-item");

        const span = document.createElement("span");
        span.href = "#";
        span.classList.add("page-link", "btn-pg");
        span.setAttribute("role", "button");

        if (index === 0) {
            li.classList.add("current-page", "active");
        }else{
            if (index === nbPage - 1) {
                start = found%length === 0 ? start + length : start + parseInt(length / 2);
            }else{
                start += length;
            }
        }
        span.innerText = index+1;
        span.setAttribute("start", start);
        li.append(span);
        ul.append(li);
    }

    const next = document.createElement("li");
    next.classList.add("page-item", "disabled");

    const spanNext = document.createElement("span");
    spanNext.href="#";
    spanNext.classList.add("page-link");
    spanNext.innerText = "Suivant";

    next.append(spanNext);
    ul.append(next);

    section.append(ul);
    return section;
    
}

export default makePagination;

export const empty = (element) => {
    while (element.lastElementChild) {
        element.removeChild(element.lastElementChild);
    }
    return element;
}

const addProduct = (product, content) => {
    const divParent = document.createElement("div");
    divParent.classList.add("col-sm", "text-center","text-white", "mx-2", "mb-3", "rounded", "shadow-hover");

    const divImg = document.createElement("div");
    const img = document.createElement("img");
    img.classList.add("mt-2")
    img.height = 200;
    img.width = 200;
    img.alt = `City apéro metz image ${product.image}`;

    img.src = images[product.image];
    divImg.append(img);

    const divInfos = document.createElement("div");
    divInfos.classList.add("mt-2");
    divInfos.innerHTML = `
        <span class="d-block h4 fw-bold">${product.price}</span>
        <span class="h6 fw-bold">${product.quantity} ${product.name} </span>
    `;

    divParent.append(divImg, divInfos);
    content.append(divParent);
}

export const drawProducts = (data, container, start, length, match) => {
    // console.log(start, length);
    if(length>match){
        length = match;
    }

    const row1  = document.createElement("div");
    const row2  = document.createElement("div");
    row1.classList.add("row");
    row2.classList.add("row");
    let item = 0
    for (let index = start; index < length; index++) {
        if (item < 4) {
            addProduct(data[index], row1);
            item ++;
        }else{
            addProduct(data[index], row2);
        }
    }
    container.append(row1);
    container.append(row2);
    
    //for animation
    row1.classList.add("faded-in","faded-out");
    row2.classList.add("faded-in", "faded-out");

    requestAnimationFrame(()=>{
        row2.classList.remove("faded-out");
        row1.classList.remove("faded-out");
    })
}


export const activePaginate = (db, containerProduct) => {
    const btnPaginations = document.querySelectorAll('.pagination .btn-pg');
    btnPaginations.forEach(btn => {
        btn.addEventListener("click", event => {
            if (!btn.parentNode.classList.contains("current-page")) {
                document.querySelector(".current-page").classList.remove("current-page","active");
                let length = db.length;
                if(parseInt(btn.getAttribute("start")) + db.length === db.match ){
                        length = db.match;
                }else if(parseInt(btn.getAttribute("start")) === 0) {
                    length = db.length
                }else{
                    length += parseInt(btn.getAttribute("start")); 
                }
                empty(containerProduct);
                drawProducts(db.data, containerProduct, parseInt(btn.getAttribute("start")), length, db.match);  
                btn.parentNode.classList.add("current-page", "active");
            }
        })
    });
}