const mobile = window.matchMedia("(max-width: 1000px)");
///////////////////////////////////////////////////////////////////////
// handle navigation navbar
const itemsMenu = document.querySelectorAll('#menu>ul li');
const contentIndex = document.querySelector('.content-index');
const cover = document.querySelector('.cover');

itemsMenu.forEach(item => {
    item.addEventListener('click', event => {
        const link = item.querySelector('a');

        const curentActive = document.querySelector("a.active");
        console.log(curentActive);
        //deleting the active class to current menu
        if(curentActive !== link){
            curentActive.classList.remove("active");
        }
        
        if(!link.classList.contains("active")){
            link.classList.add("active");

            const btnCollapsed = document.querySelector('button.navbar-toggler');
            if(!btnCollapsed.classList.contains('collapsed')){
                // btnCollapsed.setAttribute("aria-expanded", "false");
                // btnCollapsed.classList.add("collapsed");
                btnCollapsed.click();
            }

            empty(contentIndex);

            const divContainer = document.createElement('div');
            divContainer.classList.add("container", "mt-3");

            const containerProduct = document.createElement('div');
            containerProduct.classList.add('container-product');

            const containerPagination = document.createElement('div');
            containerPagination.classList.add('container-pagination');

            divContainer.append(containerProduct, containerPagination);
            contentIndex.append(divContainer);

            cover.classList.add("d-flex", "flex-column")
            contentIndex.classList.add("flex-fill", "d-flex", "align-items-center");
            if(mobile.matches){
                contentIndex.classList.add("bg-dark-costum");
            }

            empty(containerProduct)
            const db = DATA[link.getAttribute('id')];
            empty(containerPagination);

            containerPagination.append(makePagination(db.start, db.length, db.match))
            drawProducts(db.data, containerProduct, db.start, db.length, db.match); 
            activePaginate(db, containerProduct);
        }
    });
});

const linkIndex = document.querySelector(".logo");

linkIndex.addEventListener('click', event => {
    const curentActive = document.querySelector("a.active");
    if(curentActive !== linkIndex){
        curentActive.classList.remove("active");
    }

    if(!linkIndex.classList.contains("active")){
        empty(contentIndex);
        curentActive.classList.remove("active");
        contentIndex.append(cloneScooter, cloneLivraison, cloneCarousel, cloneFooter);
        linkIndex.classList.add("active");

        cover.classList.remove("d-flex", "flex-column");
        contentIndex.classList.remove("flex-fill",  "d-flex", "align-items-center");
        if(mobile.matches){
            contentIndex.classList.remove("bg-dark-costum");
        }
    }
    
});


if(mobile.matches) {
   const itemFooter = document.querySelector('.footer-item-1');
   const cols = itemFooter.querySelectorAll("div.col-4");
   cols.forEach(c => {
       c.classList.remove("col-4", "text-center");
       c.classList.add("d-flex", "justify-content-between", "align-items-center","mb-3")
    });
   itemFooter.classList.remove("row");

   const menuToggle = document.querySelector('#menu');
   const btnCollapsed = document.querySelector('button.navbar-toggler');

    window.addEventListener('scroll', event => {
        event.stopPropagation();
        if(menuToggle.classList.contains("show")){ 
            btnCollapsed.click()  
        } 
   });
}